<script>
	import { onMount } from 'svelte';
	import { bbox } from '@turf/turf';
	import Panel from './Panel.svelte';
	import Group from './Group.svelte';
	import Map from './Map.svelte';
	import MapSource from './MapSource.svelte';
	import MapLayer from './MapLayer.svelte';
	import ColChart from './charts/ColChart.svelte';
	import Loader from './ui/Loader.svelte';
	import Select from './ui/Select.svelte';
	import { getData, getNomis, getBreaks, getTopo, aggregate } from './utils.js';
	
	// CONSTANTS
	const apiurl = 'https://www.nomisweb.co.uk/api/v01/dataset/';
	const apikey = '0x3cfb19ead752b37bb90da0eb3a0fe78baa9fa055';
	const geography = 'TYPE298';
	const mapstyle = 'https://bothness.github.io/ons-basemaps/data/style-omt.json';
	const tabledata = './data/indicators.json';
	const ladtopo = {
		url: './data/lad_boundaries_2020.json',
		layer: 'LA2020EW',
		code: 'AREACD',
		name: 'AREANM'
	};
	const lsoabldg = {
		url: 'https://cdn.ons.gov.uk/maptiles/t18/tiles/{z}/{x}/{y}.pbf',
		layer: 'imddata2',
		code: 'lsoa11cd'
	};
	const lsoabounds = {
		url: 'https://cdn.ons.gov.uk/maptiles/administrative/lsoa/v1/boundaries/{z}/{x}/{y}.pbf',
		layer: 'boundaries',
		code: 'AREACD'
	};
	const lsoadata = './data/lsoa2011_lad2020.csv';
	const colors = ["#d5f690","#5bc4b1","#2e9daa","#0079a2","#005583"];
	
	// OBJECTS
	let map = null;

	// DATA
	let indicators;
	let ladbounds;
	let ladlookup;
	let ladlist;
	let lsoalookup;
	let data = {};
	let active = {
		lsoa: {
			selected: null,
			lsoahovered: null
		},
		lad: {
			selected: null,
			selectedPrev: null,
			ladhovered: null,
			ladhighlighted: null
		}
	};
	
	// STATE
	let selectItem;
	let selectMeta;
	let selectData;
	let selectLSOA;
	let selectLAD;
	let loading = true;

	let mapZoom = null;
	
	function initialise() {
		getTopo(ladtopo.url, ladtopo.layer)
		.then(geo => {
			ladbounds = geo;

			let lookup = {};
			let list = [];
			geo.features.forEach(f => {
				lookup[f.properties[ladtopo.code]] = f.properties[ladtopo.name];
				list.push({
					code: f.properties[ladtopo.code],
					name: f.properties[ladtopo.name]
				});
			});

			list.sort((a, b) => a.name.localeCompare(b.name));
			ladlist = list;

			return lookup;
		})
		.then(lookup => {
			ladlookup = lookup;
			
			getData(lsoadata)
			.then(data => {
				let lookup = {};
				data.forEach(d => {
					lookup[d.code] = {
						name: d.name,
						parent: d.parent
					};
				});
				return lookup;
			})
			.then(lookup => {
				lsoalookup = lookup;

				fetch(tabledata)
				.then(res => res.json())
				.then(json => {
					indicators = json;

					selectItem = indicators[0].children[4].children[1];
				});
			})
		});
	}

	function setSelect() {
		if (!(selectMeta && selectItem && selectMeta.code == selectItem.code)) {
			let code = selectItem.code;
			let group = indicators.find(d => d.code == code.slice(0, 3));
			let table = group.children.find(d => d.code == code.slice(0, 7));
			let cell = +code.slice(7,10);

			selectMeta = {
				code: selectItem.code,
				group: group,
				table: table,
				cell: cell
			};

			loadData();
		}
	}

	function loadData() {
		console.log('loading data...');
		loading = true;
		if (data[selectItem.code]) {
			selectData = data[selectItem.code];
			console.log('data loaded from memory!');
			loading = false;
		} else {
			getNomis(apiurl + selectMeta.table.nomis + selectMeta.cell + '&geography=' + geography + '&uid=' + apikey + '&recordlimit=2000', selectMeta.cell)
			.then(res => {
				let dataset = {
					lsoa: {},
					lad: {},
					ew: {}
				};
				res.sort((a, b) => a.perc - b.perc);
				dataset.lsoa.data = res;
				let breaks = getBreaks(res.map(d => d.perc));
				dataset.lsoa.breaks = breaks;

				dataset.lsoa.data.forEach(d => {
					if (d.perc <= breaks[2]) {
						d.color = colors[0];
					} else if (d.perc <= breaks[4]) {
						d.color = colors[1];
					} else if (d.perc <= breaks[6]) {
						d.color = colors[2];
					} else if (d.perc <= breaks[8]) {
						d.color = colors[3];
					} else {
						d.color = colors[4];
					}
				});

				let agg = aggregate(res, lsoalookup);
				dataset.lad.data = agg[0];
				dataset.lad.breaks = getBreaks(agg[0].map(d => d.perc));

				dataset.ew.data = agg[1];

				data[selectItem.code] = dataset;
				selectData = dataset;
				console.log('data loaded from nomis!');
				loading = false;
			});
		}
	}

	function setLSOA() {
		if (active.lsoa.hovered && data[selectItem.code]) {
			selectLSOA = data[selectItem.code].lsoa.data.find(d => d.code == active.lsoa.hovered);
			let ladcode = lsoalookup[active.lsoa.hovered].parent;
			selectLAD = data[selectItem.code].lad.data.find(d => d.code == ladcode);
			active.lad.highlighted = lsoalookup[active.lsoa.hovered].parent;
		} else {
			selectLSOA = null;
			selectLAD = null;
			active.lad.highlighted = null;
		}
	}

	function doSelect() {
		if (active.lad.selected != active.lad.selectedPrev) {
			active.lad.selectedPrev = active.lad.selected;

			let newdata = JSON.parse(JSON.stringify(data[selectItem.code]));
			if (active.lad.selected) {
				// re-color dataset
				newdata.lsoa.data.forEach(d => {
					if (lsoalookup[d.code].parent != active.lad.selected) {
						d.color = '#cccccc';
					}
				});
				// zoom to district on map
				let geometry = ladbounds.features.find(f => f.properties[ladtopo.code] == active.lad.selected).geometry;
				let bounds = bbox(geometry);
				map.fitBounds(bounds, { padding: 20 });
			}
			selectData = newdata;
		}
	}

	$: selectItem && setSelect(); // Update meta when selection updates
	$: (active.lsoa.hovered || active.lsoa.hovered == null) && setLSOA(); // Get data for selected LSOA
	$: (active.lad.selected || active.lad.selected == null) && doSelect();

	onMount(() => initialise());

</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
	}
	h1 {
		margin-top: 0;
	}
	p {
		margin-bottom: 0;
	}
	ul {
		padding: 0.2em 0 0 1.2em;
		margin: 0;
	}
	li {
		padding: 0.2em 0 0 0;
	}
	hr {
		border: none;
		border-top: 3px solid grey;
	}
	#infobox {
		min-height: 200px;
	}
	.muted {
		color: #888;
	}
	.mt {
		margin-top: 5px;
	}
	.text-med {
		font-size: 1.5em;
		font-weight: bold;
	}
	.text-lrg {
		font-size: 2em;
		font-weight: bold;
	}
	.grid {
		display: grid;
  	grid-gap: 12px;
		grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
		justify-items: stretch;
		width: 100%;
		margin: 0;
	}
</style>

{#if loading}
<Loader height="100vh" width="100vw" position="fixed" bgcolor="rgba(255, 255, 255, 0.7)"/>
{/if}

<Panel>
	<h1>Census Data Vis Atlas</h1>
	{#if indicators && selectItem}
	{#if selectData}
	<ColChart data={selectData.lsoa.data} yMax={+selectData.lsoa.breaks[10]} yMin={+selectData.lsoa.breaks[0]} selected={selectLSOA ? selectLSOA.code : null} xKey="code" yKey="perc" />
	{/if}
	<div id="infobox">
		{selectMeta.table.name} <small>({selectMeta.table.code})</small><br/>
		<strong class="text-med">{selectItem.name}</strong>
		<div class="grid">
			{#if selectData}
			<div>
				<hr style="border-top-color: #871A5B"/>
				<strong>England & Wales</strong><br/>
				<strong class="text-lrg">{selectData.ew.data.perc.toFixed(1)}%</strong><br/>
				<small>{selectData.ew.data.value.toLocaleString()} of {selectData.ew.data.count.toLocaleString()} {selectItem.unit.toLowerCase()}s</small>
			</div>
			{#if selectLSOA && selectLAD}
			<div>
				<hr style="border-top-color: #27A0CC"/>
				<strong>{ladlookup[lsoalookup[selectLSOA.code].parent]}</strong><br/>
				<strong class="text-lrg">{selectLAD.perc.toFixed(1)}%</strong><br/>
				<small>{selectLAD.value.toLocaleString()} of {selectLAD.count.toLocaleString()} {selectItem.unit.toLowerCase()}s</small>
			</div>
			<div>
				<hr style="border-top-color: #000000"/>
				<strong>{lsoalookup[selectLSOA.code].name.slice(-4)}</strong><br/>
				<strong class="text-lrg">{selectLSOA.perc.toFixed(1)}%</strong><br/>
				<small>{selectLSOA.value.toLocaleString()} of {selectLSOA.count.toLocaleString()} {selectItem.unit.toLowerCase()}s</small>
			</div>
			{:else}
			<div></div>
			<div></div>
			{/if}
			{/if}
		</div>
	</div>

	{#if ladlist}
	<Select options={ladlist} bind:selected={active.lad.selected} search={true} placeholder="Select a district" />
	{/if}

	<Group props={{name: "2011 Census Tables", children: indicators}} bind:selected={selectItem} expanded/>

	{/if}
</Panel>

<Map bind:map={map} style={mapstyle} minzoom={7} maxzoom={14} bind:zoom={mapZoom}>
	{#if selectData}
	<MapSource id="lsoa" type="vector" url={lsoabldg.url} layer={lsoabldg.layer} promoteId={lsoabldg.code} minzoom={7} >
		<MapLayer id="lsoa" source="lsoa" sourceLayer={lsoabldg.layer} data={selectData} type="fill" paint="{{
			'fill-color': ['case',
				['!=', ['feature-state','color'], null], ['feature-state','color'],
				'rgba(255, 255, 255, 0)'
			]
		}}" order="tunnel_motorway_casing" />
	</MapSource>
	<MapSource id="lsoa-bounds" type="vector" url={lsoabounds.url} layer={lsoabounds.layer} promoteId={lsoabounds.code} minzoom={8} >
		<MapLayer id="lsoa-fill" source="lsoa-bounds" sourceLayer={lsoabounds.layer} type="fill" paint="{{
			'fill-color': 'rgba(255, 255, 255, 0)'
		}}" hover={true} bind:hovered={active.lsoa.hovered} />
		<MapLayer id="lsoa-bounds" source="lsoa-bounds" sourceLayer={lsoabounds.layer} type="line" paint="{{
			'line-color': ['case',
				['==', ['feature-state','hovered'], true], 'rgba(0, 0, 0, 1)',
				'rgba(0, 0, 0, 0)'
			],
			'line-width': ['case',
				['==', ['feature-state','hovered'], true], 2,
				0
			]
		}}" />
	</MapSource>
	{/if}
	{#if ladbounds}
	<MapSource id="lad" type="geojson" data={ladbounds} promoteId={ladtopo.code} >
		<MapLayer id="lad" source="lad" type="line" highlight={true} highlighted={active.lad.highlighted} paint="{{
			'line-color': ['case',
				['==', ['feature-state','selected'], true], '#27A0CC',
				['==', ['feature-state','hovered'], true], 'rgba(0, 0, 0, 1)',
				['==', ['feature-state','highlighted'], true], 'rgba(128, 128, 128, 1)',
				'rgba(192, 192, 192, 1)'
			],
			'line-width': ['case',
			['==', ['feature-state','selected'], true], 2,
				['==', ['feature-state','hovered'], true], 2,
				['==', ['feature-state','highlighted'], true], 1.5,
				1
			]
		}}" order="place_state" />
		<MapLayer id="lad-fill" source="lad" type="fill" paint="{{
			'fill-color': 'rgba(255, 255, 255, 0)'
		}}" hover={true} bind:hovered={active.lad.hovered} click={true} bind:selected={active.lad.selected} maxzoom={7.99} />
	</MapSource>
	{/if}
</Map>